.hero {
  display: flex;
  align-items: center;
}
.hero__info {
  display: inline-block;
  color: #193e6c;
  background-color: #e7f6ff;
  font-size: 0.875rem;
  border-radius: 3.125rem;
  padding: 0.625rem 0.625rem;
  font-weight: 500;
  font-family: "Plus Jakarta Sans";
}
.hero__info span {
  color: #fff;
  background-color: #094b72;
  margin-right: 0.625rem;
  border-radius: 3.125rem;
  padding: 0.3125rem 1.875rem;
}
.hero__title {
  font-family: "Playfair Display";
  font-size: 5.625rem;
  color: #094b72;
  font-weight: lighter;
  letter-spacing: 0.5px;
}
.hero__paragraph {
  font-family: "Plus Jakarta Sans";
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 158%;
  color: #4f4f4f;
  width: 75%;
}

.nav {
  display: flex;
  list-style: none;
  align-items: center;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
}
.nav__actions {
  font-size: 0.75rem;
  text-transform: uppercase;
}
.nav__actions--middle {
  margin-left: 2.5rem;
}
.nav__actions a {
  text-decoration: none;
  color: black;
}
.nav__actions a .lupa {
  width: 1.875rem;
}
.nav__actions--search {
  margin-left: auto;
}
.nav__logotext {
  font-weight: 600;
  font-size: 1.25rem;
  margin-right: auto;
  text-transform: capitalize;
}
.nav__logo {
  margin-right: 0.625rem;
}

.heading {
  max-width: 69.375rem;
  margin: 0 auto;
}

.hero {
  display: flex;
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-size: 100%;
  font-family: "Playfair Display", "Plus Jakarta Sans", sans-serif;
}

img {
  display: block;
  max-width: 100%;
}